import { Button, Select } from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { useDropzone } from "react-dropzone";

import { FiUploadCloud, FiXCircle } from "react-icons/fi";
import Multiselect from "multiselect-react-dropdown";
import { MultiSelect } from "react-multi-select-component";
import Error from "components/form/Error";
import spinnerLoadingImage from "assets/img/spinner.gif";
import InputAreaTwo from "components/form/InputAreaTwo";
import SelectTimeZone from "components/form/SelectTimeZone";
import PageTitle from "components/Typography/PageTitle";
import useSettingSubmit from "hooks/useInvoiceSubmit";
import SelectCurrency from "components/form/SelectCurrency";
import LabelArea from "components/form/LabelArea";
import SelectReceiptSize from "components/form/SelectPrintSize";
import Uploader from "components/image-uploader/Uploader";
import useProductSubmit from "hooks/useProductSubmit";
import image1 from "../../assets/img/day1.png";
import image2 from "../../assets/img/day2.png";
import { useEffect, useState } from "react";
import { getAllCommonProperyList, uploadDocImage } from "services/Api";
import { useParams } from "react-router";
import { useRef } from "react";
import { notifyError, notifySuccess } from "utils/toast";

const Setting = () => {
  const { id } = useParams();
  const { t } = useTranslation();
  const fileInputRef = useRef(null);
  const [selectedFile, setSelectedFile] = useState(null);
  const [propertyList, setPropertyList] = useState([]);

  const {
    errors,
    register,
    handleSubmit,
    onSubmit,
    watch,
    isSave,
    setValue,
    isSubmitting,
  } = useSettingSubmit(id, selectedFile);

  const baseAmountWatch = watch("base_amount");
  const cgstWatch = watch("cgst");
  const sgstWatch = watch("sgst");

  const getPropertyList = async () => {
    const PropertyList = await getAllCommonProperyList();
    setPropertyList(PropertyList?.data?.data?.propertyList);
  };

  const handleFileInputChange = async (event) => {
    const file = event.target.files[0];

    const formData = new FormData();
    formData.append("file", file);
    try {
      const response = await uploadDocImage(formData);
      if (response && response.status === 200) {
        console.log(response, "response");
        setSelectedFile(response.data?.data.filePath);
        notifySuccess(response.data.message);
      }
    } catch (e) {
      notifyError(
        e.response?.data ? e.response?.data?.message : "something went wrong"
      );
    }
  };

  const handleUploadButtonClick = () => {
    // Trigger the file input dialog when the button is clicked.
    fileInputRef.current.click();
  };

  useEffect(() => {
    let total = 0;
    if (baseAmountWatch >= 0) {
      total = total + parseInt(baseAmountWatch);
    }
    if (cgstWatch >= 0) {
      total = total + parseInt(cgstWatch);
    }
    if (sgstWatch >= 0) {
      total = total + parseInt(sgstWatch);
    }
    setValue("total", total);
  }, [baseAmountWatch, cgstWatch, sgstWatch]);

  // useEffect(() => {
  //   getPropertyList();
  // }, []);

  return (
    <>
      <PageTitle>{t("Add Invoice")}</PageTitle>

      <div className="container p-6 mx-auto bg-white  dark:bg-gray-800 dark:text-gray-200 rounded-lg">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="grid grid-cols-12 font-sans">
            <div className="col-span-12 md:col-span-12 lg:col-span-12 mr-3 ">
              <div className="flex-grow scrollbar-hide w-full max-h-full pb-0">
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  {/* <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select property")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`property_id`, {
                          required: "Select property is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" hidden>
                          {t("Select property")}
                        </option>
                        {propertyList?.map((item) => (
                          <option value={item.id} key={item?.id}>
                            {item.name}
                          </option>
                        ))}
                      </Select>
                      <Error errorName={errors.property_id} pos />
                    </div>
                  </div> */}
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Invoice date")}
                    </label>
                    <div className="sm:col-span-2">
                      <InputAreaTwo
                        register={register}
                        label={t("Invoice Date")}
                        name="invoice_date"
                        type="date"
                        placeholder={t("Enter Invoice Date")}
                      />
                      <Error errorName={errors.invoice_date} pos />
                    </div>
                  </div>
                  {/* <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Vendor")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`typeIdProof`, {
                          required: "Select Vendor is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Vendor")}
                        </option>

                        <option value="Vendor 2">Vendor 2</option>
                        <option value="Vendor 3">Vendor 3</option>
                      </Select>
                      <Error errorName={errors.Selectproperty} pos />
                    </div>
                  </div> */}
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  {/* <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Category")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`category`, {
                          required: "Select Category is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Category")}
                        </option>
                        <option value="procurement">Procurement</option>
                        <option value="b_to_b_customer">BtoB customer</option>
                        <option value="b_to_c_customer">BtoC customer</option>
                      </Select>
                      <Error errorName={errors.category} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Discription")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Discription")}
                        name="discription"
                        type="text"
                        placeholder={t("Enter Discription")}
                      />
                      <Error errorName={errors.discription} pos />
                    </div>
                  </div> */}

                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Invoice Number")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Invoice Number")}
                        name="invoice_number"
                        type="text"
                        placeholder={t("Enter Invoice Number")}
                      />
                      <Error errorName={errors.invoice_number} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Status")}
                    </label>
                    <div className="sm:col-span-3">
                      <Select
                        {...register(`paymentStatus`, {
                          required: "Select Category is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Payment Status")}
                        </option>
                        <option value="paid">Paid</option>
                        <option value="unpaid">Unpaid</option>
                      </Select>
                      <Error errorName={errors.paymentStatus} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Base Amount")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Amount")}
                        name="base_amount"
                        type="number"
                        placeholder={t("Enter Amount")}
                      />
                      <Error errorName={errors.base_amount} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-1">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("CGST")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("CGST")}
                        name="cgst"
                        type="number"
                        placeholder={t("Enter CGST")}
                      />
                      <Error errorName={errors.cgst} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-1">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("SGST")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("SGST")}
                        name="sgst"
                        type="number"
                        placeholder={t("Enter SGST")}
                      />
                      <Error errorName={errors.sgst} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Total")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Total")}
                        name="total"
                        type="number"
                        placeholder={t("Enter Total")}
                      />
                      <Error errorName={errors.tds} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Bill Copy")}
                    </label>
                    <div className="">
                      <input
                        ref={fileInputRef}
                        type="file"
                        style={{ display: "none" }}
                        onChange={handleFileInputChange}
                      />
                    </div>
                    <div
                      onClick={handleUploadButtonClick}
                      className="border-2 border-gray-300 dark:border-gray-600 border-dashed rounded-md cursor-pointer px-6 pt-5 pb-6"
                    >
                      {selectedFile ? (
                        <img src={selectedFile} alt="doc" />
                      ) : (
                        <>
                          <span className="mx-auto flex justify-center">
                            <FiUploadCloud className="text-3xl text-green-500" />
                          </span>
                          <p className="text-sm mt-2">{t("DragYourImage")}</p>
                          <em className="text-xs text-gray-400">
                            {t("imageFormat")}
                          </em>
                        </>
                      )}
                    </div>
                  </div>
                </div>
                <div className="flex flex-row-reverse pb-6">
                  {isSubmitting ? (
                    <Button disabled={true} type="button" className="h-12">
                      <img
                        src={spinnerLoadingImage}
                        alt="Loading"
                        width={20}
                        height={10}
                      />{" "}
                      <span className="font-serif ml-2 font-light">
                        Processing
                      </span>
                    </Button>
                  ) : (
                    <Button type="submit" className="h-12 px-8">
                      {" "}
                      {isSave ? "Save" : "Update"}
                    </Button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </>
  );
};
export default Setting;
