import { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";

//internal import
import { SidebarContext } from "context/SidebarContext";
import SettingServices from "services/SettingServices";
import { notifyError, notifySuccess } from "utils/toast";
import {
  createVendor,
  createVendorInvoice,
  updateVendor,
  updateVendorInvoice,
} from "services/Api";
import { useHistory } from "react-router-dom";

const useSettingSubmit = (id, selectedFile, isEdit) => {
  const { setIsUpdate } = useContext(SidebarContext);
  const [isSave, setIsSave] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const history = useHistory();

  const {
    register,
    handleSubmit,
    setValue,
    watch,
    formState: { errors },
  } = useForm();

  const onSubmit = async (data) => {
    try {
      setIsSubmitting(true);
      const payload = {
        service_category: data?.category,
        service_description: data?.discription,
        invoice_number: data?.invoice_number,
        invoice_date: data?.invoice_date,
        bill_file_path: selectedFile,
        base_amount: data?.base_amount,
        sgst: data?.sgst,
        cgst: data?.cgst,
        total_amount: data?.total,
        status: data?.paymentStatus,
      };
      if (isEdit) {
        payload.vendor_id = data?.vendor_id;
        payload.id = id;
      } else {
        payload.vendor_id = id;
      }

      console.log(payload, "payload");

      let res = {};
      if (isEdit) res = await updateVendorInvoice(payload);
      else res = await createVendorInvoice(payload);
      if (res.data.status == true) {
        notifySuccess(res.data.message);
        history.goBack();
      }
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
      setIsSubmitting(false);
    }
  };

  return {
    errors,
    register,
    handleSubmit,
    onSubmit,
    isSave,
    setValue,
    isSubmitting,
    watch,
    setValue,
  };
};

export default useSettingSubmit;
